.hero__section {
  position: relative;
  // padding-top: 58.4px;
  height: 502px;
  overflow: hidden;
  @media screen and (min-width: 768px) {
    height: 399px;
  }

  @media screen and (min-width: 1200px) {
    height: 665px;
  }
}

.hero {
  background-color: $lightPinkColor;

}

.hero__box {
  position: absolute;
  top: 83px;
  right: -62px;
  width: 346px;
  height: 344px;
  border-radius: 50%;
  background: $titlePinkColor;

  @media screen and (min-width: 768px) {
    top: 0px;
    left: 292px;
    width: 324px;
    height: 322px;
  }

  @media screen and (min-width: 1200px) {
    top: 0px;
    left: 460px;
    width: 538px;
    height: 538px;
  }

}

.hero__title {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 218px;
  // top: 84px;
  // left: 100px;

  @media screen and (min-width: 768px) {
    top: 0;
    // transform: translateY(0);
    // margin-top: 102px;

    width: 162px;
  }
  @media screen and (min-width: 1200px) {
    top: 171px;
    left: 100px;
    width: 277px;
    // transform: translateY(-50%);
  }
}

.hero__text {
  font-family: 'Titan One';
  font-size: 26px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $whiteTextColor;
  @media screen and (min-width: 768px) {
    font-size: 22px;
    line-height: 1.14;
  }
  @media screen and (min-width: 1200px) {
    font-size: 38px;
    line-height: 1.16;
  }
}

.hero__text--different {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  line-height: 0.96;
  text-transform: uppercase;
  color: $accentColor;
  @media screen and (min-width: 768px) {
    font-size: 34px;
    line-height: 0.91;
  }
  @media screen and (min-width: 1200px) {
    font-size: 58px;
    line-height: 0.9;
  }
}
.hero-icecream__img {
  position: absolute;
  top: 9px;
  left: 81px;
  width: 171px;
  height: 410px;
  @media screen and(min-width:768px) {
    width: 143px;
    height: 336px;
    top: 32px;
    left: 71px;
  }

  @media screen and(min-width:1200px) {
    width: 236px;
    height: 557px;
    top: 56px;
    left: 119px;
  }
}

.hero__btn-section {
  display: flex;
  margin-top: 19px;
  @media screen and (min-width: 768px) {
    margin-top: 20px;
  }

  @media screen and (min-width: 1200px) {
    margin-top: 25px;
  }
}

.hero__btn {
  font-family: 'DM Sans Regular';
  font-size: 8px;
  line-height: 1.25;
  display: flex;
  align-items: center;
  text-align: center;
  @media screen and (min-width: 1200px) {
    font-size: 14px;
    line-height: 1.29;
  }
}

.btn-products {
  margin-right: 8px;
  @media screen and (min-width: 1200px) {
    margin-right: 17px;
  }
}


.hero-milk {
  
  @media screen and (max-width: 767.5px) {
    display: none;
  }

  @media screen and (min-width:768px) {
  display: flex;
  position: absolute;
  bottom: 28px;
  align-items: flex-end;
  }
  
  @media screen and (min-width: 1200px) {
    bottom: 45px;
  }
}

.hero-milk__img {
  width: 124px;
  height: 126px;

  @media screen and (min-width: 1200px) {
    width: 188px;
    height: 191px;
  }
}

.milk-text-section {
  display: flex;
  flex-direction: column;
}

.hero-milk__text {
  font-family: DM Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 8px;
  line-height: 1.5;
  color: $whiteTextColor;
  width: 114px;

  @media screen and (min-width: 1200px) {
    width: 200px;
    font-size: 14px;
    line-height: 1.43;
  }
}



.hero-girl__img {

  @media screen and (max-width: 767.5px) {
    display: none;
  }

  @media screen and (min-width:768px) {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 168px;
  height: 133px;
  }
  
  @media screen and (min-width: 1200px) {
    width: 293px;
    height: 232px;
  }
}

.button-arrow {
  margin-top: 7px;
  margin-bottom: 6px;
  display: block;
  border-radius: 50%;
  border: none;
  width: 18px;
  height: 18px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  @media screen and (min-width: 1200px) {
    width: 26px;
    height: 26px;
  }
}

