  .service-point {

  @media screen and (max-width: 767.5px) {
    display: none;
  }

  @media screen and (min-width:768px) {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 94px;
  right: 44px;
}
 
  @media screen and (min-width: 1200px) {
    top: 156px;
    right: 115px;
  }
}

.service-point__item {
 
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  flex-basis: 50px;
  @media screen and (min-width: 1200px) {
    flex-basis: 70px;
  }
}

.service-point__item:not(:last-child) {
  margin-bottom: 13px;
  @media screen and (min-width: 1200px) {
    margin-bottom: 15px;
  }
}
.service-point__number {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 1.14;
  text-align: right;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $accentColor;
  @media screen and (min-width: 1200px) {
    font-size: 46px;
    line-height: 1.15;
  }
  &::before {
    content: '';
    position: absolute;
    z-index: 5;
    top: -2px;
    right: -8px;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: $whiteTextColor;
    @media screen and (min-width: 1200px) {
      width: 30px;
      height: 30px;
      top: -5px;
      right: -15px;
    }
  }
}
.service-point__text {
  font-family: DM Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 8px;
  line-height: 10px;
  text-align: right;
  color: $whiteTextColor;
  @media screen and (min-width: 1200px) {
    font-size: 14px;
    line-height: 1.3;
  }
}


