.header__section {
  // @media screen and (min-width:320px) {
  //   display: none;
  // }
  display: flex;
  justify-content: space-between;
  justify-items: center;
  padding-top: 6px;

  @media screen and (min-width: 768px) {
    padding-top: 37px;
  }

  @media screen and (min-width: 768px) {
    &__burger {
      top: 52px;
      right: 244px;
    }
    &__burger.active {
      top: 23px;
      right: 34px;
    }
  }

  @media screen and (min-width: 1200px) {
    &__burger {
      display: none;
    }
  }

  &__section {
    position: relative;
    justify-content: space-between;
    padding-top: 6px;
    display: flex;

    @media screen and (min-width: 1200px) {
      padding-top: 37px;
    }
    // @media screen and (max-width: 630px) {
    //   overflow: hidden;
    // }
  }
  
  &__navigation {

    // margin-right: -35px;
    z-index: 100;

    @media screen and (max-width: 1199px) {
      background: #e17992;
      padding: 23px 21px 0 21px;
      height: 100vh;
      width: 250px;

      display: flex;
      flex-direction: column;

      position: absolute;
      right: -100%;
      top: 0;
      transition: right 250ms linear;
    }

    @media screen and (min-width: 1200px) {
      display: flex;
      flex-direction: row-reverse;
    }
  }

  &__navigation.active {
    right: 0;
  }

  &__list {
    @media screen and (min-width: 1200px) {
      display: flex;
    }
  }

  &__item {
    // margin-right: 35px;

    @media screen and (max-width: 1199px) {
      margin-bottom: 22px;
    }
    &:not(:last-child) {
      margin-right: 35px;
    }
  }

  &__link {
    font-weight: 700;
    font-size: 14px;
    line-height: 1.28;
    color: $whiteTextColor;
  }

  &__btn {
    width: 186px;
    height: 44px;
    background: $whiteTextColor;
    border-radius: 22px;
    border: none;
    margin-top: 60px;
    margin-bottom: 33px;

    font-weight: 700;
    font-size: 16px;
    line-height: 1.31;
    cursor: pointer;

    color: $accentColor;
    // @media screen and(max-width:767.5px) {
    //   display: none;
    // }
    @media screen and (min-width: 1200px) {
      margin: 0 0 0 110px;
    }
  }
  &__btn-section {
    margin-left: auto;
    margin-right: auto;
}
}

.navigation__item {
  margin-right: 35px;
}

.navigation__link {
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 1.31;
  display: flex;
  align-items: center;
  color: $whiteTextColor;
}

.mobile-menu-btn {
  margin-left: auto;
}

.header__btn {

  @media screen and (max-width:767px) {
    display: none;
  }

  @media screen and (min-width:768px){
  font-family: 'DM Sans Bold';
  font-size: 16px;
  line-height: 1.31;
  padding: 7px 13px;
  background: $whiteTextColor;
  color: $accentColor;
  text-decoration: none;
  border-radius: 22px;
  border: none;
  cursor: pointer;
  width: 160px;
  height: 44px;


  &:hover {
    background-color: $accentColor;
    color: $whiteTextColor;
  }
  }
}

  
  // background: $whiteTextColor;
